













































import Vue from "vue";
import Component from "vue-class-component";
import { Prop } from "vue-property-decorator";

@Component
export default class Comhead extends Vue {
  @Prop({ required: false })
  isRightIcon: any;
  @Prop({ required: false })
  title: any;
  @Prop({ required: false })
  isLeftIcon: any;
  @Prop({ required: false })
  rightTitle: any;
  @Prop({ required: false })
  leftTitle: any;
  @Prop({ required: false })
  leftIconName: any;
  @Prop({ required: false })
  rightIconName: any;
  @Prop({ required: false })
  lineShow: any;
  @Prop({ required: false })
  skin: any;
  @Prop({ required: false })
  contextIndex: any;

  type = "H5";
  leftScale = "1.5";
  textindex = 1;
  rightClick() {
    this.$emit("rightClick");
  }
  leftClick() {
    if (this.isLeftIcon == "icon-zuo") {
      this.$router.go(-1);
      return;
    }
    this.$emit("leftClick");
  }
  changecontextIndex(contextIndex) {
    if (contextIndex == 1) {
      //  document.getElementById('d1').scrollTop=0;
      this.textindex = 1;
    }
    if (contextIndex == 2) {
      console.log(this.$el.querySelector('tab1'))
      // location.hash="#detail";
      //  document.getElementById('d1').scrollTop=100;
      this.textindex = 2;
    }
  }
  getBgColor() {}
  mounted() {
    console.log(this.title);
    console.log(this.isRightIcon);
  }
}



